import React from 'react';
import Infobar from './Infobar';
import { PCRStoreValue } from '../db';

const defaultProfile: PCRStoreValue<'unit_profile'> = {
  height: '???',
  weigth: '???',
  age: '???',
  birth_month: '???',
  birth_day: '???',
  blood_type: '???',
  race: '???',
  guild: '???',
  favorite: '???',
  voice: '???',
} as any;

interface profileProps {
  profile?: PCRStoreValue<'unit_profile'>;
}

function profile(props: profileProps) {
  const { profile = defaultProfile } = props;

  return (
    <>
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.height + 'cm'}
      />
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.weight + 'kg'}
      />
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.age + ''}
      />
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.birth_month + '' + profile.birth_day + ''}
      />
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.blood_type + ''}
      />
      <Infobar
        size="medium"
        width={50}
        label=""
        value={profile.race}
      />
      <Infobar
        size="medium"
        width={100}
        label=""
        value={profile.guild}
      />
      <Infobar
        size="medium"
        width={100}
        label=""
        value={profile.favorite}
      />
      <Infobar
        size="medium"
        width={100}
        label="CV"
        value={profile.voice}
      />
    </> 
  );
}

export default profile;
