import React from 'react';
import Infobar from './Infobar';
import { Property } from '../DBHelper/property';
// import { getFightingCapacity } from '../DBHelper/helper';

const allPropertyKeys: (keyof Property)[] = [
  'atk', // 2
  'magic_str', // 4
  'def', // 3
  'magic_def', // 5
  'physical_critical', // 6
  'magic_critical', // 7
  'hp', // 1
  'life_steal', // 9
  'wave_hp_recovery', // 10
  'wave_energy_recovery', // 11
  'accuracy', // 17
  'dodge', // 8
  'physical_penetrate', // 12()
  'magic_penetrate', // 13()
  'energy_recovery_rate', // 14
  'energy_reduce_rate', // 16()
  'hp_recovery_rate', // 15
];

const keyLabel = {
  hp: 'HP',
  atk: '',
  def: '',
  magic_str: '',
  magic_def: '',
  physical_critical: '',
  magic_critical: '',
  dodge: '',
  life_steal: 'HP',
  wave_hp_recovery: 'HP',
  wave_energy_recovery: 'TP',
  physical_penetrate: '',
  magic_penetrate: '',
  energy_recovery_rate: 'TP',
  hp_recovery_rate: '',
  energy_reduce_rate: 'TP',
  accuracy: '',
};

const keyAbbrLabel = {
  hp: 'HP',
  atk: '',
  def: '',
  magic_str: '',
  magic_def: '',
  physical_critical: '',
  magic_critical: '',
  dodge: '',
  life_steal: 'HP',
  wave_hp_recovery: 'HP',
  wave_energy_recovery: 'TP',
  physical_penetrate: '',
  magic_penetrate: '',
  energy_recovery_rate: 'TP',
  hp_recovery_rate: '',
  energy_reduce_rate: 'TP',
  accuracy: '',
};

interface CharaStatusProps {
  property?: Partial<Property>;
  partial?: boolean;
  abbr?: boolean;
  // showFightingCapacity?: boolean;
}

function CharaStatus(props: CharaStatusProps) {
  let property: Partial<Property> = props.property || {};
  let keys = props.partial ? Object.keys(props.property || {}) as (keyof Property)[] : allPropertyKeys;
  if (!props.property) {
    for (let key of keys)  property[key] = '???' as any;
  }
  return (
    <>
      {keys.map(key => (
        <Infobar
          key={key}
          size={props.abbr ? 'medium': 'large'}
          width={50}
          label={props.abbr ? keyAbbrLabel[key] : keyLabel[key]}
          value={property[key]}
        />
      ))}
      {/* {props.showFightingCapacity && (
        <Infobar
          key="fighting_capacity"
          size="large"
          label=""
          value={props.property ? getFightingCapacity(props.property as Property) : '???'}
        />
      )} */}
    </>
  );
}

export default CharaStatus;
